
import { MultiLanguage } from '../../../Infra/Language'

import EmailField from '../../../Components/Fields/Email'
import Popup from '../../Popup'

export default class {

    // here comes all interactable elements Id´s 
    static elements() {
        return {
            formEmail: 'formEmail',
            emailID: 'email',
            btnNext: 'btnNext',
            btnPrev: 'btnPrev',
        };
    }

    static getHtml() {
        var elements = this.elements();

        var nextMsg = MultiLanguage(16);
        var stepMsg = MultiLanguage(17);
        var prevMsg = MultiLanguage(22);
        var placeholderEmail = MultiLanguage(20);
        var placeholderEmail_label = MultiLanguage(21);

        return `<div style="width:296px;" class="form-row-group-dark"> 
                    <div style='min-height:375px'>
                        <br>                        
                        <h3 style='color:#009E96'>${stepMsg} 2/4</h3>
                        <br>            
                        <div class="form-row-group with-icons" align="left" id='registerIdForm'>
                            <span style='padding-left:20px'><b>${placeholderEmail_label}</b></span>
                            ${EmailField.getHtml(elements.emailID, placeholderEmail)}
                            <br>
                        </div>
                        <br>
                    </div>
                    <div>
                        <table width='100%'>
                            <tr>
                                <td><a id="${elements.btnPrev}" class="button circle block green">${prevMsg} </a></td>                                    
                                <td width='20px'></td>
                                <td><a id="${elements.btnNext}" class="button circle block green">${nextMsg} <i class="fa fa-spinner fa-spin" id='loading' style="display:none;"></i></a></td>
                            </tr>
                        </table>                        
                    </div>
                    <br>
                    <br>                    
                </div>${Popup.getHtml()}`;
    }

    static validate(_params) {
        var elements = this.elements();
        
        if (!EmailField.validate(elements.formMail, _params))
            return false;

        return true;
    }
}
