
import {
    getCurrentLanguage,
    MultiLanguageChange,
} from '../Infra/Language'

import {
    Endpoints,
    socialID_Input,
} from '../Infra/Endpoints'

import {
    SetLanguageHTMLSelect,
    updateHTML,
    IsLoading,
    loadingOn,
    loadingOff,
    postPublicPortal,
    setToSession,
} from '../Infra/Util'

import FormSID from '../Components/Forms/Register/RegisterID'
import FormEmail from '../Components/Forms/Register/RegisterEmail'

function GetRegisterVars() {
    return {
        // reserved words
        register_state: 'register_state',
        payload: 'payload',
        // states
        formSID: 'formSID',
        formEmail: 'formEmail',
        formPass: 'formPass',
    }
}

export default class {

    getHtml() { return "<div id='currentForm'></div>" }

    constructor(params) {
        this.params = params;
       
        // -----------------------------------------------------------------------------
        // page events here --------------------------------------------------------
        // -----------------------------------------------------------------------------

        $(document).ready(function () {
            var states = GetRegisterVars();            
            UpdateNewView(states.formSID);
            SetLanguageHTMLSelect()
        });

        $(document).on('change', '#languageSel', function () {
            if (MultiLanguageChange($('#languageSel').val()))
                setTimeout(() => { location.href = '/register'; }, 100);
        });

        $(document).on('keydown', function (e) {
            var states = GetRegisterVars();
            var currentState = GetCurrentView();
            var _valParams = { focus: false, msg: false, fields: null }
            switch (e.keyCode) {
                // tab
                case 9:
                    switch (currentState) {
                        case states.formSID: FormSID.validate(_valParams); break;
                        case states.formEmail: FormEmail.validate(_valParams); break;
                    }
                    break;
                // enter
                case 13:
                    switch (currentState) {
                        case states.formSID: mainSubmit(FormSID.elements().btnNext); break;
                        case states.formEmail: mainSubmit(FormSID.elements().btnNext); break;
                    }
                    break;
            }
        });

        $(document).bind('click', function (e) {
            mainSubmit($(e.target).attr('id'));
        });

        // -----------------------------------------------------------------------------
        // functions here --------------------------------------------------------
        // -----------------------------------------------------------------------------

        function GetCurrentView() {
            return sessionStorage.getItem(GetRegisterVars().register_state);
        }

        function PushState(newState) {
            sessionStorage.setItem(GetRegisterVars().register_state, newState);
        }

        function UpdateNewView(newState) {
            PushState(newState)
            var states = GetRegisterVars();
            // HTML
            switch (newState) {
                case states.formSID: updateHTML('currentForm', FormSID.getHtml()); break;
                case states.formEmail: updateHTML('currentForm', FormEmail.getHtml()); break;
            }
            // POST OP.            
            switch (newState) {
                case states.formSID: FormSID.maskUp(); break;
            }
        }

        function mainSubmit(click_id) {
            var states = GetRegisterVars();
            var currentState = GetCurrentView();
            // ACTIONS
            switch (currentState) {
                case states.formSID: {
                    var elements = FormSID.elements();
                    var formData = socialID_Input(
                        $('#' + elements.socialID).val(),
                        getCurrentLanguage()
                    );
                    switch (click_id) {
                        case elements.btnNext: btnNext_formSID_Click(formData); break;
                    }
                    break;
                }
                case states.formEmail: {
                    var elements = FormEmail.elements();
                    break;
                }
            }
        }

        // -----------------------------------------------------------------------------
        // formSID functions
        // -----------------------------------------------------------------------------

        function btnNext_formSID_Click(formData) {
            if (IsLoading()) return;
            var elements = FormSID.elements();
            loadingOn('#' + elements.btnNext)

            // mock!! ------------
            setToSession('mockServer', JSON.stringify({ payload: true, obj: { } }));
            // mock!! ------------

            postPublicPortal(Endpoints().socialID, formData)
                .then(resp => {
                    if (resp.ok == true)
                        flowSocialID_Next()
                    else
                        displaySystemPopup(MultiLanguage(5), resp.msg);
                })
                .catch(resp => {
                    displaySystemPopup(MultiLanguage(5), resp.msg)
                });
        }

        function flowSocialID_Next() {
            var states = GetRegisterVars();
            loadingOff()
            UpdateNewView(states.formEmail);
        }

        // -----------------------------------------------------------------------------
        // formEmail functions
        // -----------------------------------------------------------------------------


        // -----------------------------------------------------------------------------
        // formPass
        // -----------------------------------------------------------------------------

    }
}
