
import { MultiLanguage } from '../../../Infra/Language'

import StringText from '../../../Components/Fields/String'
import Popup from '../../Popup'
import LanguageSelect from '../../LanguageSelect'

export default class {

    // here comes all interactable elements Id´s 
    static elements() {
        return {
            formID: 'formID',
            cpfID: 'cpf',
            nameID: 'name',
            btnNext: 'btnNext',            
        };
    }

    static maskUp() {
        var elements = this.elements();
        $("#" + elements.cpfID).mask("999.999.999-99");
    }

    static getHtml() {

        var elements = this.elements();

        var registerMsg = MultiLanguage(3);
        var nextMsg = MultiLanguage(16);
        var stepMsg = MultiLanguage(17);

        var placeholderID = MultiLanguage(18);
        var placeholderID_label = MultiLanguage(19);
        var placeholderName = MultiLanguage(20);
        var placeholderName_label = MultiLanguage(21);

        return `<div style="width:296px;" class="form-row-group-dark"> 
                    <div style='min-height:375px'>
                        <br>
                        <table><tr><td width='120px'><b>${registerMsg}</b></td><td width='50%'>${LanguageSelect.getHtml()}</td></tr></table>
                        <h3 style='color:#009E96'>${stepMsg} 1/4</h3>
                        <br>            
                        <div class="form-row-group with-icons" align="left" id='registerIdForm'>
                            <span style='padding-left:20px'><b>${placeholderID_label}</b></span>
                            ${StringText.getHtml(elements.cpfID, placeholderID)}
                            <br>
                            <span style='padding-left:20px'><b>${placeholderName_label}</b></span>
                            ${StringText.getHtml(elements.nameID, placeholderName)}
                            <br>
                        </div>
                        <br>
                    </div>
                    <div>
                        <a id="${elements.btnNext}" class="button circle block green">${nextMsg} <i class="fa fa-spinner fa-spin" id='loading' style="display:none;"></i></a>
                    </div>
                    <br>
                    <br>                    
                </div>${Popup.getHtml()}`;
    }

    static validate(_params) {
        var elements = this.elements();
        
        var cpfField = StringText.validateCPF(elements.cpfID, _params)
        var nameField = StringText.validate(elements.nameID, _params)

        if (!(cpfField && nameField))
          return false;

        return true;
    }
}
